          {name: '销额偏差', prop: 'xepc', needOrder: true, order: '0', isPresent: true, needArrow: false},
          {name: '偏差排名', prop: 'pcpm', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '同比排名', prop: 'tbpm', needOrder: true, order: '0', isPresent: false, needArrow: false}],
        tableContent: [
          // {dp: '上海水星1店', xe: '2000', tq: '1600', tb: '55', pc: '35.4', zb: '30.7'},
