          // {time: '本日', mb: '2000', xe: '1600', dc: '55', tb: '35.4'},
          // {time: '本月', mb: '2400', xe: '1800', dc: '30', tb: '-25.4'},
          // {time: '年季', mb: '2600', xe: '1400', dc: '30', tb: '-55.4'},
          // {time: '本年', mb: '2800', xe: '1200', dc: '60', tb: '65.4'}
        ]
      },
      dpQsfxTitle: '趋势分析',
      dpJgfxTitle: '结构分析',
      dpXsfxIndex: 0,
      dpQsfxIndex: 0,
      dpJgfxIndex: 0, // 店铺结构分析，tabs的索引
      jgfxTypeList: [{name: '大类', value: '大类'}, {name: 'SPU', value: 'SPU'}],
      jgfxTypeListDay: [{name: 'SPU', value: 'SPU'}],
      jgfxTableDataInit: [],
      jgfxTableData: {
        tableHeader: [
          {name: '大类', prop: 'dl', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '销额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '毛利率', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: false},
          {name: '偏差', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
        ],
        tableContent: [
          // {dl: '套件', sl: '1600', xe: '1600', tq: '1690', tb: '-35.4', zb: '10.7'},
          // {dl: '被芯', sl: '1400', xe: '1700', tq: '1790', tb: '35.4', zb: '-30.7'},
          // {dl: '凉席', sl: '1200', xe: '1800', tq: '1690', tb: '-35.4', zb: '20.7'},
          // {dl: '夏被', sl: '1600', xe: '1900', tq: '1390', tb: '35.4', zb: '60.7'}
        ]
      },
      jgfxTableDataHeaderMon: [
        {name: '大类', prop: 'dl', needOrder: false, order: '0', isPresent: false, needArrow: false},
        // {name: '数量', prop: 'sl', needOrder: true, order: '2', isPresent: false, needArrow: false},
        {name: '销额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '毛利率', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: false},
        {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
      ],
      selectedJgfxType: 'SPU',
      dpJgfxTypeIndex: 0
    }
  },
  computed: {
    qsfxTwoLineOption () {
      const option = {
        color: ['#326DFF', '#D1A226'],
        'legend': {
          show: true,
          selectedMode: false,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 1,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': '达成',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'show': true,
            'axisLabel': {
              show: true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: '{value}%'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: true,
              lineStyle: {
                color: '#213A6B'
              }
            }
          }
        ],
        series: [
          {
            type: 'line',
            name: '实际',
            smooth: true,
            symbol: 'none',
            yAxisIndex: 0,
            lineStyle: {
              color: '#FFBE14',
              type: 'solid',
              width: px2px(2)
            },
            data: []
          },
          {
            type: 'line',
            name: '目标',
            yAxisIndex: 0,
            symbol: 'none',
            smooth: true,
            lineStyle: {
              color: '#326DFF',
              type: 'solid',
              width: px2px(2)
            },
            data: []
          }]
      }
      const labelList = []
      const sjList = []
      const mbList = []
      for (let item of this.dpLineData) {
        labelList.push(item.label)
        sjList.push(item.sj)
        mbList.push(item.mb)
      }
      option.xAxis[0].data = labelList
      option.series[0].data = sjList
      option.series[1].data = mbList
      return option
    },
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    },
    userIdentityInit () {
      return this.$store.state.userIdentityInit
    },
    selectedDpName () {
      return this.$store.state.selectedDpName
    },
    selectedZgsName () {
      const authList = this.$store.state.authList
      let str = ''
      if (this.userIdentityInit === '2') {
        for (let item of authList) {
          if (item.shop === this.selectedDpName) {
            str = item.role
            break
          }
        }
      } else if (this.userIdentityInit === '3') {
        for (let item of authList) {
          if (item.role === this.selectedDpName) {
            str = item.zgs
            break
          }
        }
      }
      return str
    }
  },
  methods: {
    tabsType ({activeValue, name, type, index}) {
      if (type === 'sale-xsfx') {
        this.dpXsfxIndex = index
        this.dealDpXsfxTableData()
      } else if (type === 'sale-jgfx-up') {
        this.dpJgfxIndex = index
        this.dealDpjgfxTableData()
      } else if (type === 'sale-qsfx') {
        this.dpQsfxIndex = index
        this.dealDpQsfxLineData()
      }
    },
    changeTableOrder ({header, type}) {
      let tempTable = {tableHeader: [], tableContent: []}
      if (type === 'sale-xsfx') {
        tempTable = _.cloneDeep(this.dpXsfxTableData)
      } else if (type === 'sale-jgfx') {
        tempTable = _.cloneDeep(this.jgfxTableData)
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'sale-xsfx') {
        this.dpXsfxTableData.tableHeader = headerList
        this.dpXsfxTableData.tableContent = tableContent
      } else if (type === 'sale-jgfx') {
        this.jgfxTableData.tableHeader = headerList
        this.jgfxTableData.tableContent = tableContent
      }
    },
    getQsLineOption (arr) {
      const option = {
        color: ['#326DFF', '#D1A226', '#FF0200'],
        'legend': {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
