                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': '达成',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'axisLabel': {
              'show': true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: '{value}%'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          },
          {
            'type': 'value',
            'name': this.qsfxIndex === 0 ? '同比' : '毛利',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'show': true,
            'axisLabel': {
              show: true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: '{value}%'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: true,
              lineStyle: {
                color: '#213A6B'
              }
            }
          }
        ],
        series: [
          {
            type: 'line',
            name: '达成',
            smooth: true,
            symbol: 'none',
            yAxisIndex: 0,
            lineStyle: {
              color: '#326DFF',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          },
          {
            type: 'line',
            name: this.qsfxIndex === 0 ? '同比' : '毛利率',
            symbol: 'none',
            smooth: true,
            yAxisIndex: 1,
            lineStyle: {
              color: '#FFBE14',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          },
          {
            type: 'line',
            name: '年累计达成',
            yAxisIndex: 0,
            symbol: 'none',
            lineStyle: {
              color: '#FF0200',
              type: 'dashed',
              width: px2px(4)
            },
            data: []
          }]
      }
      const labelList = []
      const dcList = []
      const tbList = []
      const nljdcList = []
      for (let item of arr) {
        labelList.push(item.label)
        dcList.push(item.dc)
        tbList.push(item.tb)
        nljdcList.push(item.nljdc)
      }
      option.xAxis[0].data = labelList
      option.series[0].data = dcList
      option.series[1].data = tbList
      option.series[2].data = nljdcList
      return option
    },
    changeJgfxType ({activeTypeName, index, type}) {
      this.dpJgfxTypeIndex = index
      // 档切换大类的时候，要重新查询结构分析数据
      this.dealDpjgfxTableData()
    },
    // 下面是查询数据的方法
    getAllData () {
      this.getDpXsglTableData(this.selectedZgsName, this.selectedDpName)
      this.getDpXsfxTableData(this.selectedZgsName)
    },
    // 查询 店铺，销售概览数据
    async getDpXsglTableData (companyName, dpName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.saleShop.getDpXsglTableData,
        {dateStrD: this.dateStr, dateStrM: this.dateStrM, dateStrJ: this.dateStrJ, subName: companyName, cusName: dpName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.time = item.period_wid
          if (item.period_wid === '本日') {
            obj.mb = '--'
            obj.dc = '--'
            obj.tb = '--'
            obj.xe = formatNumber(initNum(item.output_amt), 2)
            obj.mlv = (initNum(item.margin_rate) * 100).toFixed(1)
          } else {
            obj.mb = formatNumber(initNum(item.target_amt), 2)
            obj.xe = formatNumber(initNum(item.output_amt), 2)
            obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
            obj.tb = (initNum(item.output_amt_yoy) * 100).toFixed(1)
            obj.mlv = (initNum(item.margin_rate) * 100).toFixed(1)
          }
          list.push(obj)
        }
        if (list.length > 0) {
          this.dpXsglTableData = {
            tableHeader: [{name: '时间', prop: 'time', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '目标', prop: 'mb', needOrder: false, order: '2', isPresent: false, needArrow: false},
              {name: '销额', prop: 'xe', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '达成', prop: 'dc', needOrder: false, order: '0', isPresent: true, needArrow: false},
              {name: '同比', prop: 'tb', needOrder: false, order: '0', isPresent: true, needArrow: true},
              {name: '毛利率', prop: 'mlv', needOrder: false, order: '0', isPresent: true, needArrow: false, newLine: false}
            ],
            tableContent: list.sort(compareSort('mb', true))
          }
        } else {
          this.dpXsglTableData = {
            tableHeader: [{name: '时间', prop: 'time', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '目标', prop: 'mb', needOrder: false, order: '2', isPresent: false, needArrow: false},
              {name: '销额', prop: 'xe', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '达成', prop: 'dc', needOrder: false, order: '0', isPresent: true, needArrow: false},
              {name: '同比', prop: 'tb', needOrder: false, order: '0', isPresent: true, needArrow: true},
              {name: '毛利率', prop: 'mlv', needOrder: false, order: '0', isPresent: true, needArrow: false, newLine: false}
            ],
            tableContent: []
          }
        }
      }
    },
    // 查询 店铺 销售分析 表格数据
    async getDpXsfxTableData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.saleShop.getDpXsfxTableData,
        {dateNum: getMonthDayNum(this.dateStr), dateStrD: this.dateStr, dateStrM: this.dateStrM, dateStrJ: this.dateStrJ, subName: companyName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.dpXsfxTableDataInit = result
        // 要根据选择的月，季，年累，年，进行数据的整理
        this.dealDpXsfxTableData()
      }
    },
    dealDpXsfxTableData () {
      const list = []
      let dataList = []
      if (this.dpXsfxIndex === 0) {
        dataList = this.dpXsfxTableDataInit.resD || []
      } else if (this.dpXsfxIndex === 1) {
        dataList = this.dpXsfxTableDataInit.resM || []
      } else if (this.dpXsfxIndex === 2) {
        dataList = this.dpXsfxTableDataInit.resJ || []
      } else if (this.dpXsfxIndex === 3) {
        dataList = this.dpXsfxTableDataInit.resNl || []
      } else if (this.dpXsfxIndex === 4) {
        dataList = this.dpXsfxTableDataInit.resY || []
      }
      if (this.dpXsfxIndex === 0) {
        for (let item of dataList) {
          const obj = {}
          obj.dp = item.customer_name
          obj.xe = formatNumber(initNum(item.output_amt), 2)
          obj.xepc = (initNum(item.deviation) * 100).toFixed(1)
          obj.pcpm = formatNumber(initNum(item.order_deviation), 0)
          obj.tb = (initNum(item.output_amt_yoy) * 100).toFixed(1)
          obj.tbpm = formatNumber(initNum(item.order_output_amt_yoy), 0)
          list.push(obj)
        }
      } else {
        for (let item of dataList) {
          const obj = {}
          obj.dp = item.customer_name
          obj.xe = formatNumber(initNum(item.output_amt), 2)
          obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
          obj.dcpm = formatNumber(initNum(item.order_yield_rate), 0)
          obj.tb = (initNum(item.output_amt_yoy) * 100).toFixed(1)
          obj.tbpm = formatNumber(initNum(item.order_output_amt_yoy), 0)
          list.push(obj)
        }
      }
      const headerList = [
        {name: '店铺', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '销额', prop: 'xe', needOrder: true, order: '2', isPresent: false, needArrow: false},
        {name: '销额偏差', prop: 'xepc', needOrder: true, order: '0', isPresent: true, needArrow: false},
        {name: '偏差排名', prop: 'pcpm', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '同比排名', prop: 'tbpm', needOrder: true, order: '0', isPresent: false, needArrow: false}
      ]
      this.dpXsfxTableData = {
        tableHeader: this.dpXsfxIndex === 0 ? headerList : this.dpXsfxTableDataHeaderMon,
        tableContent: list.sort(compareSort('xe', false))
      }
      // 设置第一行被选中， 从而查询下面线图和表格数据
      // 获取第一条数据，查询下面的线图和店铺表格数据
      if (this.dpXsfxTableData.tableContent.length > 0) {
        const firstName = this.selectedDpName
        this.dpQsfxTitle = '趋势分析-' + firstName
        this.dpJgfxTitle = '结构分析-' + firstName
        // 这里要设置表格第一行默认选中
        // this.$eventBus.$emit('resetSelectedRow', {type: 'sale-dp-xsfx', name: firstName})
        if (this.dpXsfxIndex === 0) {
          // 查询双线图数据
          this.getDpQsfxLineDayData(firstName)
        } else {
          // 查询三线图数据
          this.getDpQsfxLineMonData(firstName)
        }
        // 还要查询分类的数据
        this.getJgfxTableData(this.selectedZgsName, this.selectedDpName)
      } else {
        this.dpQsfxTitle = '趋势分析'
        this.dpJgfxTitle = '结构分析'
        this.dpLineData = []
        this.dpQsfxLineData = []
        // 设置表格数据为空
        this.jgfxTableData.tableContent = []
      }
    },
    async getDpQsfxLineDayData (dpName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.saleShop.getDpQsfxLineDayData,
        {dateStrD: this.dateStr, dateStrM: this.dateStrM, cusName: dpName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.resD) {
          const obj = {}
          obj.label = item.period_wid
          obj.mb = initNum(item.yield_rate) * 100
          obj.sj = initNum(item.target_yield_rate) * 100
          list.push(obj)
        }
        if (list.length > 0) {
          this.dpLineData = list
        } else {
          this.dpLineData = []
        }
      }
    },
    async getDpQsfxLineMonData (dpName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.saleShop.getDpQsfxLineMonData,
        {dateStrD: this.dateStr, dateStrM: this.dateStrM, cusName: dpName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.dpQsfxLineDataInit = result.resM
        this.dealDpQsfxLineData()
      }
    },
    dealDpQsfxLineData () {
      const list = []
      for (let item of this.dpQsfxLineDataInit) {
        const obj = {}
        obj.label = item.period_wid
        obj.dc = initNum(item.yield_rate) * 100
        if (this.dpQsfxIndex === 0) {
          obj.tb = initNum(item.output_amt_yoy) * 100
        } else {
          obj.tb = initNum(item.margin_rate) * 100
        }
        obj.nljdc = initNum(item.nl_yield_rate) * 100
        list.push(obj)
      }
      if (list.length > 0) {
        this.dpQsfxLineData = list
      } else {
        this.dpQsfxLineData = []
      }
    },
    /*
  这里还要请求大类的区分
   */
    async getJgfxTableData (companyName, dpName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.saleShop.getDpjgfxTableData,
        {dateNum: getMonthDayNum(this.dateStr), dateStr: this.dateStr, dateStrM: this.dateStrM, dateStrJ: this.dateStrJ, subName: companyName, cusName: dpName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.jgfxTableDataInit = result
        this.dealDpjgfxTableData()
      }
    },
    dealDpjgfxTableData () {
      const list = []
      let dataList = []
      let proStr = 'resDl'
      if (this.dpJgfxTypeIndex === 0) {
        proStr = 'resDl'
      } else {
        proStr = 'resSpu'
      }
      if (this.dpJgfxIndex === 0) {
        dataList = this.jgfxTableDataInit[proStr + 'D'] || []
      } else if (this.dpJgfxIndex === 1) {
        dataList = this.jgfxTableDataInit[proStr + 'M'] || []
      } else if (this.dpJgfxIndex === 2) {
        dataList = this.jgfxTableDataInit[proStr + 'J'] || []
      } else if (this.dpJgfxIndex === 3) {
        dataList = this.jgfxTableDataInit[proStr + 'NL'] || []
      } else if (this.dpJgfxIndex === 4) {
        dataList = this.jgfxTableDataInit[proStr + 'Y'] || []
      }
      if (this.dpJgfxIndex === 0) {
        for (let item of dataList) {
          const obj = {}
          obj.dl = item.type_name
          obj.xe = formatNumber(initNum(item.output_amt), 2)
          obj.tq = formatNumber(initNum(item.output_amt_lym), 2)
          obj.tb = (initNum(item.output_amt_yoy) * 100).toFixed(1)
          obj.mlv = (initNum(item.margin_rate) * 100).toFixed(1)
          obj.pc = (initNum(item.deviation) * 100).toFixed(1)
          obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
          list.push(obj)
        }
      } else {
        for (let item of dataList) {
          const obj = {}
          obj.dl = item.type_name
          obj.xe = formatNumber(initNum(item.output_amt), 2)
          // obj.sl = formatNumber(initNum(item.output_qty), 0)
          obj.tq = formatNumber(initNum(item.target_amt), 2)
          obj.tb = (initNum(item.output_amt_yoy) * 100).toFixed(1)
          obj.mlv = (initNum(item.margin_rate) * 100).toFixed(1)
          obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
          list.push(obj)
        }
      }
      const headerList = [
        {name: '大类', prop: 'dl', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '销额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '毛利率', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: false},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
      ]
      this.jgfxTableData = {
        tableHeader: this.dpJgfxIndex === 0 ? headerList : this.jgfxTableDataHeaderMon,
        tableContent: list.sort(compareSort('xe', false))
      }
    }
  },
  created () {
    this.$store.commit('updateHeaderTitle', '销售')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '店铺', URL: '销售'})
    if (this.dateStr !== '') {
      this.getAllData()
    }
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  components: {
    Tabs,
    pageTitle,
    MinTitle,
    BarLineChart,
    TableList,
    TypeList
  }
}
</script>

