<template>
  <div class="tabs">
    <ul class="tabs-item">
      <li :class="index === activeIndex ? 'active' : ''" v-for="(item, index) in tabsData" :key="index" @click="changeType(index, item)" :style="withObj">{{item.name}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Title',
  props: {
    tabsData: {
      type: Array,
      default: () => [
        {
          name: '考核主体分析',
          value: '考核主体'
        },
        {
          name: '大类分析',
          value: '大类'
        }
      ]
    },
    switchType: {
      type: String,
      default: ''
    },
    defaultIndex: {
      type: String,
      default: '0'
    }
  },
  data () {
    return {
      activeIndex: 0,
      activeTypeName: ''
    }
  },
  computed: {
    withObj () {
      return { width: (100 / this.tabsData.length) + '%' }
    }
  },
  methods: {
    changeType (index, item) {
      this.activeIndex = index
      this.activeTypeName = item.value
      this.$emit('activeTypeName', {activeTypeName: this.activeTypeName, index: this.activeIndex, type: this.switchType})
    }
  },
  created () {
    this.$eventBus.$on('resetTypeList', () => {
      if (this.tabsData.length > 0) {
        this.activeIndex = 0
        this.activeTypeName = this.tabsData[0].value
      }
    })
    // this.$emit('activeTypeName', this.tabsData[0].value)
  }
}
</script>

<style lang="scss" scoped>
.tabs {
  .tabs-item {
    display: flex;
    align-items: center;
    width: 100%;
    line-height: px2rem(80);
    font-size: px2rem(28);
    font-family: PingFangSC-Regular;
    background: #0C1446;
    justify-content: space-around;
    li {
      height: 100%;
      min-width: px2rem(100);
      text-align: center;
      color: #71768C;
      border-bottom: 2px solid #0C1446;
      &.active {
        color: #FFFFFF;
        border-bottom: 2px solid #4C9AFF;
      }
    }
  }
}
</style>
